<template>
  <div class="hours">
      <div v-for="(hour, index) in hours"
           :key="index"
           :data-hour="hour"
           class="hour"
           :style="{height: heightOfSlot*12 + 'px'}"
      >
        <div v-for="n in 12"
             :key="n"
             :data-minute="(n-1)*5"
             class="minuteSlot"
             @dragover="dragOverSlot"
             @drop="getDropSlot"
             @dragenter="(event)=>event.target.classList.add('highlight')"
             @dragleave="(event)=>event.target.classList.remove('highlight')"
             @dragstart="sendIdDragBlock"
        >
        </div>
      </div>
    </div>
</template>
<script>
  export default {
    name: 'Day',
    props: {
      day: Object,
      hours: Array,
      heightOfSlot: Number
    },
    methods: {
      dragOverSlot: function (event) {
        event.preventDefault();
        event.dataTransfer.dropEffect = 'move';
      },
      getDropSlot: function (event) {
        event.target.classList.remove('highlight');
        const minute = event.target.dataset.minute;
        const hour = event.target.closest('[data-hour]').dataset.hour;
        const day = this.day.dayOfMonth;
        const month = this.day.month;
        const year = this.day.year;
        const date = {
          year: year,
          month: month,
          day: day,
          hour: hour,
          minute: minute
        };
        this.$emit('sendDnDData', date);
      },
      sendIdDragBlock: function (event) {
        const idDragBlock = event.target.dataset.id;
        this.$emit('sendIdDragBlock', idDragBlock);
      }
    }
  }
</script>
<style lang="scss">
.hours {
  width: 100px;
  border-width: 0 1px 0 0;
  border-color: black;
  border-style: solid;
  box-sizing: border-box;
  .hour {
    display: flex;
    flex-wrap: wrap;
    border-bottom: 1px solid black;
    box-sizing: border-box;
    .minuteSlot {
      position: relative;
      width: 100%;
      &:nth-child(odd) {
        background-color: lightgray;
      }
      &.highlight {
        background-color: blueviolet;
      }
    }
  }
}
</style>
