<template>
  <div class="week">
    <div class="header">
      <div class="firstColumn">
        <div class="name"></div>
        <div class="number"></div>
        <div class="gap"></div>
      </div>
      <div v-for="(day, index) in days"
           :key="index"
           class="column-header"
      >
        <div class="name">{{day.dayOfWeek}}</div>
        <div class="number">{{day.dayOfMonth}}</div>
        <div class="gap"></div>
      </div>
    </div>
    <div class="body">
      <div class="firstColumn">
        <div v-for="(hour, index) in hours"
             :key="hour"
             class="titleOfString"
             :style="{height: heightOfSlot*12 + 'px'}"
        >
          <span v-if="index !== 0">{{hour}}:00</span>
        </div>
      </div>
      <Day v-for="(day, index) in days"
           :key="index"
           :day = "day"
           :hours = hours
           :data-day="day.dayOfMonth"
           :heightOfSlot = heightOfSlot
           @sendDnDData="sendDnDData"
           @sendIdDragBlock="sendIdDragBlock"
      ></Day>
    </div>
  </div>
</template>
<script>
import Day from '@/components/Day.vue'

export default {
  name: 'Week',
  components: {Day},
  props: {
    startDay: Date,
    daysOfWeek: Array,
    startHour: Number,
    numbersOfHours: Number,
    heightOfSlot: Number
  },
  computed: {
    days: function () {
      let days = [];
      for (let i = 0; i<7; i++) {
        const plusOneDay = new Date(this.startDay);
        plusOneDay.setDate(plusOneDay.getDate() + i);
        const day = {
          dayOfMonth: plusOneDay.getDate(),
          dayOfWeek: this.daysOfWeek[i],
          month: plusOneDay.getMonth(),
          year: plusOneDay.getFullYear()
        }
        days.push(day);
      }
      return days;
    },
    hours: function () {
      let hours = [];
      for (let i = 0; i<this.numbersOfHours; i++) {
        const hour = this.startHour + i;
        if (hour <= 24) {
          hours.push(hour);
        }
      }
      return hours
    }
  },
  methods: {
    sendDnDData: function (date) {
      this.$emit('sendDnDData', date);
    },
    sendIdDragBlock: function (idDragBlock) {
      this.$emit('sendIdDragBlock', idDragBlock)
    }
  }
}
</script>
<style lang="scss">
.week {
  text-align: center;
  .firstColumn {
    width: 50px;
  }
  .titleOfString {
    border-width: 0 1px 0 0;
    border-style: solid;
    font-size: 10px;
    span {
      display: block;
      transform: translateY(-5px);
    }
  }
  .header {
    display: flex;
    .name {
      font-size: 10px;
      height: 20px;
    }
    .number {
      height: 20px;
    }
    .gap {
      height: 15px;
      border-width: 0 1px 1px 0;
      border-color: black;
      border-style: solid;
    }
    .column-header {
      width: 100px;
    }
  }
  .body {
    display: flex;
  }
}
</style>
