<template>
  <div id="app">
    <div>
      <Week :startDay = startDay
            :daysOfWeek = daysOfWeek
            :startHour = startHour
            :numbersOfHours = numbersOfHours
            :heightOfSlot = heightOfSlot
            @sendDnDData="catchDnDData"
            @sendIdDragBlock="catchIdDragBlock"
      ></Week>
    </div>
  </div>
</template>

<script>
import Week from '@/components/Week.vue'

export default {
  name: 'App',
  components: {Week},
  mounted() {
    this.setTasks();
  },
  data() {
    return {
      startDay: new Date(2020, 10, 22),
      daysOfWeek: ["ПН", "ВТ", "СР", "ЧТ", "ПТ", "СБ", "ВС"],
      startHour: 10,
      numbersOfHours: 9,
      heightOfSlot: 5,
      tasks: [
        {title: "Заголовок 1", "startDate": 1606124100000, "endDate": 1606126200000},
        {title: "Заголовок 2", "startDate": 1606216200000, "endDate": 1606221000000},
        {title: "Заголовок 3", "startDate": 1606303800000, "endDate": 1606306800000}
      ],
      changeTasks: [],
      idDragBlock: ''
    }
  },
  computed: {
    readyTasks: function () {
      const readyTasks = [];
      let counterId = 1;
      this.tasks.forEach(task => {
        const readyTask = {};
        readyTask.id = counterId++;
        readyTask.title = task.title;
        readyTask.startDate = task.startDate;
        readyTask.duration = ((( new Date(task.endDate) - new Date(task.startDate) )/1000)/60)/5;//Количество пятиминутных слотов
        readyTasks.push(readyTask);
      });
      return readyTasks;
    }
  },
  methods: {
    setTasks: function () {
      const oldTasks = document.querySelectorAll('.task');
      const oldTasksArray = [...oldTasks];
      oldTasksArray.forEach(task => task.remove());
      let tasks = [];
      if (this.changeTasks.length !== 0) {
        tasks = this.changeTasks
      } else {
        tasks = this.readyTasks
      }
      tasks.forEach(task => {
        const slot = this.getSlot(task.startDate);
        const duration = task.duration;
        const taskBlock = document.createElement('div');
        taskBlock.classList.add('task');
        taskBlock.dataset.id = task.id;
        taskBlock.style.height = this.heightOfSlot * duration + 'px';
        taskBlock.innerText = task.title;
        taskBlock.draggable = true;
        slot.append(taskBlock);
      })
    },
    getSlot: function (start) {
      const startDate = new Date(start);
      const targetDay = startDate.getDate();
      const targetHour = startDate.getHours();
      const remainderOfMinutes = startDate.getMinutes()%5;
      const targetMinute = startDate.getMinutes() - remainderOfMinutes;
      const week = document.querySelector('.week');
      const days = week.querySelectorAll('[data-day]');
      const daysArray = [...days];
      let day;
      daysArray.forEach(d => {
        if (+d.dataset.day === +targetDay) {
          day = d;
        }
      });
      const hours = day.querySelectorAll('[data-hour]');
      const hoursArray = [...hours];
      let hour;
      hoursArray.forEach(h => {
        if (+h.dataset.hour === +targetHour) {
          hour = h;
        }
      });
      const minutes = hour.querySelectorAll('[data-minute]');
      const minutesArray = [...minutes];
      let minute;
      minutesArray.forEach(m => {
        if (+m.dataset.minute === +targetMinute) {
          minute = m;
        }
      });
      return minute;
    },
    catchDnDData: function (date) {
      const newStartDate = new Date(date.year, date.month, date.day, date.hour, date.minute).getTime();
      this.changeTasks = [...this.readyTasks];
      this.changeTasks.forEach(task => {
        if (+task.id === +this.idDragBlock) {
          task.startDate = newStartDate;
          this.setTasks();
        }
      })
    },
    catchIdDragBlock: function (id) {
      this.idDragBlock = id;
    }
  }
}
</script>

<style lang="scss">
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  margin-top: 20px;
  .task {
    position: absolute;
    top: 0;
    left: 0;
    width: 90%;
    background-color: aqua;
    font-size: 10px;
    text-align: left;
    padding-left: 10px;
    box-sizing: border-box;
    z-index: 1;
    cursor: move;
  }
}
</style>
